import { useState, useEffect } from "react";
import { Modal, ModalFooter, Row, Col, Button } from "react-bootstrap";

export function SidePanel(props) {
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const [data, setData] = useState(null);

  useEffect(() => {
    const requestOptions = {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    };
    fetch(
      "https://dev.thegrowingdeveloper.org/slots/getAvailableSlots",
      requestOptions
    )
      .then((response) => response.json())
      .then((jsonRes) => {
        setData(jsonRes);
      });
  }, []);

  return (
    <div>
      <Modal show={show} onHide={handleClose} animation={true} centered>
        <Modal.Header closeButton>
          <Modal.Title>Modal heading</Modal.Title>
        </Modal.Header>
        <div className="appointment-form mx-auto">
          <form action="" className="">
            <Row>
              <Row>
                <Col sm={6}>
                  <input type="text" name="" id="" placeholder="First Name" />
                </Col>

                <Col sm={6}>
                  <input type="text" name="" id="" placeholder="Last Name" />
                </Col>
              </Row>

              <Col sm={12}>
                <input type="email" name="" id="" placeholder="Email" />
              </Col>

              <Col sm={12}>
                <input
                  type="text"
                  name=""
                  id=""
                  placeholder="Phone(WhatsApp preferred)"
                />
              </Col>

              <Col sm={12}>
                <textarea
                  name=""
                  id="form-query"
                  cols="60"
                  rows="6"
                  placeholder="Query"
                ></textarea>
              </Col>
            </Row>
          </form>
        </div>
        <Modal.Footer className="form-footer">
          <button className="proceed modal-proceed">Proceed to Payment</button>
        </Modal.Footer>
      </Modal>

      <div className="Slots">
        <h2>
          {props.date?.getDate()}/{props.date?.getMonth() + 1}/
          {props.date?.getFullYear()}
        </h2>
        <div className="timings">
          {data != null
            ? data["1-1-2022"][0]?.map((slot) => (
                <button className="available-slots">
                  {slot.start_time}-{slot.end_time}
                </button>
              ))
            : ""}
        </div>
        <div>
          <button className="proceed slot-proceed mt-3" onClick={handleShow}>
            Proceed
          </button>
        </div>
      </div>
    </div>
  );
}
